.promo-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.promo-img {
  width: 50%;
  height: auto;
}

.promo-label {
  font-weight: bold;
  margin-top: 10px;
  font-size: 1.3em; /* Aumenta el tamaño del nombre */
}

.promo-description {
  margin-top: 5px;
}

.promo-price {
  margin-top: 30px;
  margin-left: 70px;
  font-weight: bold;
  font-size: 1.5em; /* Aumenta el tamaño del precio */
}

.increment-button {
  margin-top: 10px;
  align-self: stretch;
  font-size: 1em; /* Aumenta el tamaño del texto del botón */
  padding: 20px; /* Aumenta el tamaño del botón */
}
